<script>
export default {
  name: "createStaff-component",
  data() {
    let validateId = (rule, value, callback) => {
      const reg = /[a-zA-Z0-9]/;
      if (!reg.test(value)) {
        callback(new Error("格式錯誤"));
      } else {
        callback();
      }
    };
    return {
      form: {
        id: "", // 身分證字號
        p_Name: "", // 姓名
        sex: "", // 性別
        blood: "", // 血型
        birthday: "", // 生日
        country: "", // 國別
        s_Id: "", // 廠商統一編號
        email: "", // 電子郵件
        address: "", // 地址
        tel1: "", // 電話
        tel2: "", // 行動電話
        dept_Name: "", // 部門
        job_Title: "", // 職稱
        urgent_Contact: "", // 緊急聯絡人
        urgent_Tel: "", // 緊急聯絡人電話
        insurance_Begin: "",
        insurance_End: "",
      },
      rules: {
        id: [
          { required: true, message: "必填", trigger: "blur" },
          { validator: validateId, trigger: "blur" },
        ],
        p_Name: [{ required: true, message: "必填", trigger: "blur" }],
        sex: [{ required: true, message: "必填", trigger: "change" }],
        blood: [{ required: true, message: "必填", trigger: "change" }],
        birthday: [{ required: true, message: "必填", trigger: "change" }],
        country: [{ required: true, message: "必填", trigger: "change" }],
        email: [
          { required: false, message: "必填", trigger: "blur" },
          { type: "email", message: "信箱格式錯誤", trigger: "blur" },
        ],
        address: [{ required: true, message: "必填", trigger: "blur" }],
        tel1: [{ required: true, message: "必填", trigger: "blur" }],
        tel2: [{ required: true, message: "必填", trigger: "blur" }],
        dept_Name: [{ required: false, message: "必填", trigger: "blur" }],
        job_Title: [{ required: false, message: "必填", trigger: "blur" }],
        urgent_Contact: [{ required: true, message: "必填", trigger: "blur" }],
        urgent_Tel: [{ required: true, message: "必填", trigger: "blur" }],
      },
    };
  },
  computed: {
    newVendorInfo() {
      this.$store.state.newVendorInfo.forEach((e) => {
        e.label = `${e.name} ${e.e_Name}`;
      });
      return this.$store.state.newVendorInfo;
    },
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.insEmployee();
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    // 新增資料
    async insEmployee() {
      try {
        await this.$store.dispatch("insEmployee", this.form);
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "新增成功",
        });
        this.$router.push({ name: "staffManagement" });
      } catch (err) {
        console.log(err);
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // API取得資料
    async getNewVendorInfo() {
      try {
        await this.$store.dispatch("getNewVendorInfo");
      } catch (err) {
        console.log(err);
      }
    },
  },
  async mounted() {
    this.getNewVendorInfo();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    this.form.s_Id = this.$route.params.s_id;
    this.form.insurance_Begin = this.$route.params.insurance_Begin;
    this.form.insurance_End = this.$route.params.insurance_End;
  },
};
</script>

<template src="./createStaff.html"></template>
